.other-div {
  background-color: rgba(207, 205, 219, 0.295);
}

.other-list {
  list-style: none;
}

@media only screen and (min-width: 440px) {
  .other-list {
    -webkit-column-count: 2;
    -moz-column-count: 2;
    column-count: 2;
  }
}

@media only screen and (max-width: 440px) {
  .other-list {
    -webkit-column-count: 1;
    -moz-column-count: 1;
    column-count: 1;
  }
}

.other-list li {
  padding: 4px;
}

.other-list li i {
  color: #113e6d;
}
